import React from "react";
import "./App.css";
import { FiEdit, FiTrash } from "react-icons/fi";
import { FaShoppingBasket } from "react-icons/fa";

class App extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			todoList: [],
			activeItem: {
				id: null,
				title: "",
				completed: false,
			},
			editing: false,
		};
		this.fetchItems = this.fetchItems.bind(this);
		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.getCookie = this.getCookie.bind(this);

		this.startEdit = this.startEdit.bind(this);
		this.deleteItem = this.deleteItem.bind(this);
		this.strtikeUnstrike = this.strtikeUnstrike.bind(this);
	}

	getCookie(name) {
		let cookieValue = null;
		if (document.cookie && document.cookie !== "") {
			const cookies = document.cookie.split(";");
			for (let i = 0; i < cookies.length; i++) {
				const cookie = cookies[i].trim();
				// Does this cookie string begin with the name we want?
				if (cookie.substring(0, name.length + 1) === name + "=") {
					cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
					break;
				}
			}
		}
		return cookieValue;
	}

	componentWillMount() {
		this.fetchItems();
	}
	fetchItems() {
		console.log("Fetching...");

		fetch("http://127.0.0.1:8000/api/item-list/")
			.then((response) => response.json())
			.then((data) => {
				this.setState({
					todoList: data,
				});
			});
	}

	handleChange(e) {
		let name = e.target.name;
		let value = e.target.value;
		console.log("name:", name, " value:", value);

		this.setState({
			activeItem: {
				...this.state.activeItem,
				title: value,
			},
		});
	}

	handleSubmit(e) {
		e.preventDefault();
		console.log("item: ", this.state.activeItem);

		let csrftoken = this.getCookie("csrftoken");
		let url = "http://127.0.0.1:8000/api/item-create/";

		if (this.state.editing === true) {
			url = `http://127.0.0.1:8000/api/item-update/${this.state.activeItem.id}/`;
			this.setState({
				editing: false,
			});
		}

		fetch(url, {
			method: "POST",
			headers: {
				"Access-Control-Allow-Origin": "*",
				"Content-Type": "application/json",
				"X-CSRFToken": csrftoken,
			},
			body: JSON.stringify(this.state.activeItem),
		})
			.then((response) => {
				this.fetchItems();
				this.setState({
					activeItem: {
						id: null,
						title: "",
						completed: false,
					},
				});
			})
			.catch(function (error) {
				console.log("ERROR: ", error);
			});
	}

	startEdit(item) {
		this.setState({
			activeItem: item,
			editing: true,
		});
	}

	deleteItem(item) {
		let csrftoken = this.getCookie("csrftoken");

		fetch(`http://127.0.0.1:8000/api/item-delete/${item.id}/`, {
			method: "DELETE",
			headers: {
				"Access-Control-Allow-Origin": "*",
				"Content-Type": "application/json",
				"X-CSRFToken": csrftoken,
			},
		}).then((response) => {
			this.fetchItems();
		});
	}

	strtikeUnstrike(item) {
		item.completed = !item.completed;

		let csrftoken = this.getCookie("csrftoken");
		let url = `http://127.0.0.1:8000/api/item-update/${item.id}/`;
		fetch(url, {
			method: "POST",
			headers: {
				"Access-Control-Allow-Origin": "*",
				"Content-Type": "application/json",
				"X-CSRFToken": csrftoken,
			},
			body: JSON.stringify({ completed: item.completed, title: item.title }),
		}).then((response) => {
			this.fetchItems();
		});

		console.log("Item:", item.completed);
	}

	render() {
		let items = this.state.todoList;
		let self = this;
		return (
			<div className="container">
				<div id="item-container">
					<div id="form-wrapper">
						<form id="form" onSubmit={this.handleSubmit}>
							<h2>
								&nbsp;&nbsp;&nbsp; Shopping List &nbsp;
								<FaShoppingBasket />
							</h2>
							<div className="flex-wrapper">
								<div style={{ flex: 6 }}>
									<input
										onChange={this.handleChange}
										type="text"
										id="title"
										className="form-control"
										name="title"
										value={this.state.activeItem.title}
										placeholder="Add an item"
									/>
								</div>
								<div style={{ flex: 1 }}>
									<input
										id="submit"
										className="btn btn-warning"
										type="submit"
										name="Add"
									/>
								</div>
							</div>
						</form>
					</div>
					<div id="list-wrapper">
						{items.map((item, index) => {
							return (
								<div key={index} className="item-wrapper flex-wrapper">
									<div
										onClick={() => this.strtikeUnstrike(item)}
										style={{ flex: 7 }}
									>
										{item.completed == false ? (
											<span>{item.title}</span>
										) : (
											<div>
												<FaShoppingBasket />
												<strike>{item.title}</strike>
											</div>
										)}
									</div>
									<div style={{ flex: 1 }}>
										<button
											onClick={() => self.startEdit(item)}
											className="btn btn-sm btn-outline-info"
										>
											<FiEdit />
										</button>
									</div>
									<div style={{ flex: 1 }}>
										<button
											onClick={() => self.deleteItem(item)}
											className="btn btn-sm btn-outline-dark delete"
										>
											<FiTrash />
										</button>
									</div>
								</div>
							);
						})}
					</div>
				</div>
			</div>
		);
	}
}

export default App;
